#news {
    width: 100%;
    padding: 150px 0px;
    background: #efefef;

    .inner {
        width: 1180px;
        margin: 0 auto;

        h1 {
            font: bold 30px/1 'arial';
            color: #333;
            text-align: center;
            margin-bottom: 50px;
        }

        ul {
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-content: flex-start;

            li {
                width: 48%;
                padding: 40px;
                border: 1px solid #ccc;
                margin-bottom: 20px;

                h2 {
                    font: bold 18px/1 'arial';
                    color: #555;
                    margin-bottom: 10px;
                }

                p {
                    font: 12px/1.2 'arial'; color: #777;
                }
            }
        }
    }
}